<template>
    <div>
        <go-back />
        <h1 class="divided">称号大全</h1>
        <div v-if="loading && collectionLoading">
            <load-indicator loadText="正在加载称号"></load-indicator>
        </div>
        
        <template v-if="!collectionLoading">
            <h3 style="text-align:center;">收藏进度</h3>
            <table style="margin:0 auto;">
                <thead>
                    <th>阶段</th>
                    <th>收集数</th>
                    <th>奖励称号</th>
                    <th>称号描述</th>
                </thead>
                <tr v-for="(v,k) in collection" :key="k">
                    <td>{{v.id}}</td>
                    <td>{{v.goal}}</td>
                    <td><span :style="titleStyle(v.title._TextInColor,v.title._TextOutColor)">{{v.title.name}}</span></td>
                    <td>{{v.title.description}}</td>
                </tr>
            </table>
            </template>

            <h3>图鉴</h3>
            <div class="filter">
                搜索
                <input  type="text" placeholder="输入称号名称搜索..." v-model="searchName"/>
                <button @click="searchData">查询</button>
                <button @click="resetfield">重置</button>
                <div class="grade-filter">
                    <label>筛选</label>
                    <span class="normal">
                        <input id="filter-fixstate" type="checkbox" v-model="fixstate" />
                        <label for="filter-fixstate">英雄称号</label>
                    </span>
                    <span class="epic">
                        <input id="filter-quest" type="checkbox" v-model="filter.quest" />
                        <label for="filter-quest">任务</label>
                    </span>
                    <span class="magic">
                        <input id="filter-costume" type="checkbox" v-model="filter.costume" />
                        <label for="filter-costume">时装</label>
                    </span>
                    <span class="ancient">
                        <input id="filter-period" type="checkbox" v-model="filter.period" />
                        <label for="filter-period">期限</label>
                    </span>
                    <span class="rare">
                        <input id="filter-activity" type="checkbox" v-model="filter.activity" />
                        <label for="filter-activity">活动</label>
                    </span>
                    <span class="unique">
                        <input id="filter-accompany" type="checkbox" v-model="filter.accompany" />
                        <label for="filter-accompany">伴侣</label>
                    </span>
                    <span class="divine">
                        <input id="filter-guild" type="checkbox" v-model="filter.guild" />
                        <label for="filter-guild">公会</label>
                    </span>
                    <span class="legendary">
                        <input id="filter-vip" type="checkbox" v-model="filter.vip" />
                        <label for="filter-vip">会员</label>
                    </span>
                    <span class="magic">
                        <input id="filter-other" type="checkbox" v-model="filter.other" />
                        <label for="filter-other">其他</label>
                    </span>
                </div>
            </div>

            <div v-if="loading && !collectionLoading">
                <load-indicator loadText="正在搜索"></load-indicator>
            </div>

            <template v-if="!loading">
                <table style="margin-top:1em;" border="2">
                    <thead>
                        <th>称号</th>
                        <th>描述</th>
                        <th>属性</th>
                        <th>英雄称号属性</th>
                        <th>关联物品</th>
                        <th>期限天数</th>
                    </thead>
                        <tr v-for="(v,k) in result.titles" :key="k" style="height:75px;">
                            <td><span :style="titleStyle(v._TextInColor,v._TextOutColor)">{{v.name}}</span></td>
                            <td>{{v.description}}</td>
                            <td v-if="v.state.length>0">
                                <stat-grid :statSet="statSet(v.state)"/>
                            </td>
                            <td v-else>无</td>
                            <td v-if="v.fixState.length>0">
                                <stat-grid :statSet="statSet(v.fixState)"/>
                            </td>
                            <td v-else>无</td>
                            <td v-if="v.item">
                                <item-icon-tooltip
                                    :itemId="v.item.id" 
                                    :itemStub="v.item"
                                    :count="1"
                                    :noLink="noLink">
                                </item-icon-tooltip>
                            </td>
                            <td v-else>无</td>
                            <td v-if="v.days!=0">{{v.days}}天</td>
                            <td v-else>无期限</td>
                        </tr>
                </table>
                <pager :pageSize="pageSize" v-model="pageNo" @on-jump="jump" style="float:right;"></pager>
        </template>
        <back-to-top><span class="topButton"><i class="fa fa-arrow-up"></i></span></back-to-top>
    </div>
</template>
<script>
import Vue from 'vue';
import GameInfo from '../api/GameInfo';
import GoBack from "@/components/util/GoBack.vue";
import Loader from "@/components/util/Loader";
import ItemStat from "@/old/api/item/itemstat";
import StatGrid from "@/old/game/StatGrid";
import ItemIconTooltip from '@/old/items/ItemIconTooltip.vue';
import pager from '@/components/util/Pager';
import BackToTop from '@/components/util/BacktoTop';
Vue.component("load-indicator", Loader);
Vue.component("back-to-top", BackToTop);

export default {
    components: {
        GoBack,
        StatGrid,
        ItemIconTooltip,
        pager
    },
    data(){
        return{
            result:{
                titles:null,
            },
            loading:true,
            collectionLoading:true,
            noLink:false,
            searchName:null,
            collection:null,
            pageSize: 30,
            pageNo: 1,
            fixstate:false,
            filter:{
                quest:false,
                costume:false,
                period:false,
                activity:false,
                accompany:false,
                guild:false,
                other:false,
                vip:false,
            }
        }
    },
    created(){
        this.fetchData();
    },
    watch:{
        hasParams(val){
            if(!val){
                this.fetchData();
            }
        }
    },
    computed:{
        sendParams(){
            let typeParams = "";
            if(this.filter.quest){
                typeParams += "'0',";
            }
            if(this.filter.costume){
                typeParams += "'1',";
            }
            if(this.filter.period){
                typeParams += "'2',";
            }
            if(this.filter.activity){
                typeParams += "'11',";
            }
            if(this.filter.accompany){
                typeParams += "'5','12',";
            }
            if(this.filter.guild){
                typeParams += "'4','10',";
            }
            if(this.filter.vip){
                typeParams += "'3',";
            }
            if(this.filter.other){
                typeParams += "'6','7','8','9',";
            }
            const paramsObj = {
                name:this.searchName? this.searchName : undefined,
                fixstate : this.fixstate? true: undefined,
                type:typeParams?typeParams:undefined,
            }

            return paramsObj;
        },
        hasParams(){
            if(this.searchName || this.fixstate){
                return true;
            }
            
            if(this.filter.quest || this.filter.costume || this.filter.period
            || this.filter.activity || this.filter.accompany || this.filter.guild || this.filter.vip || this.filter.other){
                return true;
            }
            return false;
        }
    },
    methods:{
        jump(id){
            this.pageNo = id;
            this.fetchData();
        },
        fetchData(){
            if(GameInfo.titleCache[this.pageNo] && !this.hasParams){
                this.result = GameInfo.titleCache[this.pageNo];
                this.collection = GameInfo.titleCache[1].reward;
                this.loading = false;
                this.collectionLoading = false;
            }else{
                this.loading = true;
                GameInfo.titleInfo(this.pageNo,this.sendParams,this.$store.state.regionCode,
                    (res) => {
                        this.pageSize = Math.floor(res.count/10) + 1;
                        this.result = res;
                        this.loading = false;
                        this.collection = res.reward;
                        this.collectionLoading = false;
                    },
                    (err) => {
                        this.error = err;
                        this.loading = false;
                        this.collectionLoading = false;
                });
            }
        },
        statSet(state) {
            let ret = ItemStat.joinStatSet(state,"value");
            return ret;
        },
        titleStyle(textincolor,textoutcolor){
            //const title = this.loading.result!;
            const fg = `color: ${GameInfo.hexToColor(textincolor)}`;
            const bg = `text-shadow: 0px 0px 2px ${GameInfo.hexToColor(textoutcolor)}`;
            return `${fg};${bg};`;
            //return '';
        },
        searchData(){
            if(!this.hasParams) return;
            this.loading = true;
            this.pageNo = 1;
            this.fetchData();
        },
        resetfield(){
            this.searchName = '';
            this.pageNo = 1;
            this.pageSize = Math.floor(GameInfo.titleCache[1].count/10) + 1; 
            this.fixstate = false;
            this.filter.quest = false;
            this.filter.costume = false;
            this.filter.period = false;
            this.filter.activity = false;
            this.filter.accompany = false;
            this.filter.guild = false;
            this.filter.vip = false;
            this.filter.other = false;
        }
    }
}
</script>
<style lang="less" scoped>
@import "../../less/core.less";
button{
    height:1.8em;
    margin-left:5px;
}

.topButton{
  width: 60px;
  height: 60px;
  padding: 10px 10px;
  font-size: 22px;
  border:1px solid @dv-c-idle;
}
</style>