<template>
    <div>
        <h1 class="divided">好感度</h1>
        <go-back />
         <div v-if="loading">
                <load-indicator loadText="正在加载中"></load-indicator>
        </div>
        <template v-if="!loading">
            <div class="stats">
                <table class="labryinth-table">
                    <thead>
                        <th>NPC</th>
                        <th>NPC位置</th>
                        <th>联盟</th>
                        <th>赠送礼物</th>
                        <th>回礼</th>
                        <th>收集奖励</th>
                        <th>信息</th>
                    </thead>
                        <tr v-for="(v,k) in result.list" :key="k">
                            <td>
                                <npc-icon :IconId="v.npcIcon"></npc-icon>
                                {{v.name}}
                            </td>
                            <td>{{v.map}}</td>
                            <td>{{v.unionName}}</td>
                            <td>
                                <item-icon-tooltip
                                    :itemId="v.gift.id" 
                                    :itemStub="v.gift"
                                    :count="1"
                                    :noLink="noLink">
                                </item-icon-tooltip>
                            </td>
                            <td>
                                <div v-for="(vv,kk) in v.presentMail" :key="kk">
                                    <div class="Title">
                                        概率: {{vv.prob/10000000}}%
                                        <div v-for="(vvv,kkk) in vv.item" :key="kkk" class="titleitem">
                                            <item-icon-tooltip
                                                    :itemId="vvv.itemId" 
                                                    :itemStub="vvv.item"
                                                    :count="vvv.count"
                                                    :noLink="noLink">
                                            </item-icon-tooltip>
                                        </div>
                                    </div>
                                </div>
                            </td>
                            <td>
                                好感度达到{{v._NpcPresentRepute1/v.totalFavour *100}}%
                                <item-icon-tooltip
                                    :itemId="v._NpcPresentID1" 
                                    :itemStub="v.PresentItem1"
                                    :count="1"
                                    :noLink="noLink">
                                </item-icon-tooltip>
                                <span v-if="v._NpcPresentRepute2!=0">
                                好感度达到{{v._NpcPresentRepute2/v.totalFavour *100}}% 
                                <item-icon-tooltip
                                    :itemId="v._NpcPresentID2" 
                                    :itemStub="v.PresentItem2"
                                    :count="1"
                                    :noLink="noLink">
                                </item-icon-tooltip>
                                </span>
                            </td>
                            <td><span style="white-space: pre-wrap;">{{v.info}}</span></td>
                        </tr>
                </table>
                <pager :pageSize="pageSize" v-model="pageNo" @on-jump="jump" style="float:right;"></pager>
            </div>    
        </template>
    </div>
</template>
<script>
import Vue from 'vue';
import GameInfo from '../api/GameInfo';
import GoBack from "@/components/util/GoBack.vue";
import ItemIconTooltip from '@/old/items/ItemIconTooltip.vue';
import Loader from "@/components/util/Loader";
import NpcIcon from "@/components/NPC/NpcIcon"
import pager from '@/components/util/Pager';
Vue.component("load-indicator", Loader);

export default {
    created(){
        this.fetchData();
    },
    components: {
        GoBack,
        ItemIconTooltip,
        NpcIcon,
        pager
    },
    data(){
        return{
            result:null,
            noLink:false,
            loading:true,
            pageSize: 30,
            pageNo: 1,
            cacheArray:[],
        }
    },
    methods:{
        jump(id){
            this.pageNo = id;
            this.fetchData();
        },
        fetchData(){
            if(this.cacheArray[this.pageNo]){
                this.result = this.cacheArray[this.pageNo];
                this.loading = false;
            }else{
            this.loading = true;
            GameInfo.Repute(this.pageNo,this.$store.state.regionCode,
                    (res) => {
                        this.cacheArray[this.pageNo] = res;
                        this.pageSize = Math.floor(res.count/10) + 1;
                        this.result = res;
                        this.loading = false;
                    },
                    (err) => {
                        this.error = err;
                        this.loading = false;
                    });
            }
        }
    }
}
</script>
<style lang="less" scoped>
@import "../../less/core.less";
.stats {
    overflow-x: auto;
    overflow-y:hidden;
    max-width: 250px;
    @media only screen and (min-width: 450px) {
        max-width: 380px;
    }

    @media only screen and (min-width: 550px) {
        max-width: 500px;
    }

    @media only screen and (min-width:650px) {
        max-width: 400px;
    }

    @media only screen and (min-width:780px) {
        max-width: 500px;
    }

    @media only screen and (min-width:900px) {
        max-width: 644px;
    }

    @media only screen and (min-width:@min-desktop-wide-width) {
        max-width: 950px;
    }
}

.Title {
    display: flex;
    flex-direction: row;

    .titleitem {
        flex: 0 0 auto;
    }
}

.labryinth-table{
    margin:0 auto;
}
</style>