<template>
    <div class="donation">
        <h1 class="divided">捐款图鉴</h1>
        <go-back />
        <div v-if="loading">
                <load-indicator loadText="正在加载中"></load-indicator>
        </div>
        <div style="display:flex; align-items:center;">
            <div>
                <table border="1" v-if="!loading">
                    <thead>
                        <th>排名</th>
                        <th>奖励</th>
                    </thead>
                        <tr v-for="(item,index) in result" :key="index">
                            <td>{{item.grade}}</td>
                            <td>
                                <div class="Title">
                                <div class="titleitem"  v-for="(v,k) in item.items" :key="k">
                                    <item-icon-tooltip
                                        :itemId="v.item.id" 
                                        :itemStub="v.item"
                                        :count="v.count"
                                        :noLink="noLink">
                                    </item-icon-tooltip>
                                    <!-- <template v-if="v.item.type.appellationId">
                                        <title-info :titleId="v.item.type.appellationId"/>
                                    </template> -->
                                </div>
                                </div>
                            </td>
                        </tr>
                </table>
            </div>
            <div style="margin-left:8rem;">
                <shop-browser :shopId="shopId"  :useClick="useClick" />
            </div>
        </div>
    </div>
</template>
<script>
import GameInfo from '../api/GameInfo';
import ItemIconTooltip from '@/old/items/ItemIconTooltip.vue';
import GoBack from "@/components/util/GoBack.vue";
import TitleInfo from "@/components/title/TitleInfo.vue";
import ShopBrowser from '@/components/item/shop/ShopBrowser.vue';
export default {
    components: {
        ItemIconTooltip,
        GoBack,
        TitleInfo,
        ShopBrowser
    },
    data(){
        return{
            result: null,
            error: null,
            loading: true,
            noLink: false,
            shopId: 46001,
            useClick: true
        }
    },
    created(){
        this.fetchData();
    },
    methods:{
        fetchData(){
            if(GameInfo.donationCache){
                this.result = GameInfo.donationCache;
                this.loading = false;
            }else{
                GameInfo.getDonation(this.$store.state.regionCode,
                    (res) => {
                        this.result = res;
                        this.loading = false;
                    },
                    (err) => {
                        this.error = err;
                        this.loading = false;
                    });
                }
            }
    }
}
</script>
<style lang="less" scoped>
.Title {
    display: flex;
    flex-direction: row;

    .titleitem {
        flex: 0 0 auto;
    }
}
</style>