<template>
    <div>
        <h1 class="divided">主线奖励</h1>
        <go-back />
         <div v-if="loading">
                <load-indicator loadText="正在加载中"></load-indicator>
        </div>
        <template v-if="!loading">
            <p>本页只关注主线章节完成奖励</p>
            <div>
                <h3>奖励</h3>
                    <table border="1" class="labryinth-table">
                        <thead>
                            <th>章节</th>
                            <th>奖励</th>
                            <th>邮件</th>
                        </thead>
                        <tr v-for="(v,k) in result.list1" :key="k">
                            <td>{{v.chapterName}}</td>
                            <td>
                                <div class="Title">
                                    <div v-for="(vv,kk) in v.reward" :key="kk" class="titleitem">
                                        <item-icon-tooltip
                                                :itemId="vv.itemId" 
                                                :itemStub="vv.item"
                                                :count="vv.count"
                                                :noLink="noLink">
                                        </item-icon-tooltip>
                                    </div>
                                </div>
                            </td>
                            <td>
                                <div class="Title">
                                    <div v-for="(vv,kk) in v.mailItems" :key="kk" class="titleitem">
                                        <item-icon-tooltip
                                                :itemId="vv.itemId" 
                                                :itemStub="vv.item"
                                                :count="vv.count"
                                                :noLink="noLink">
                                        </item-icon-tooltip>
                                    </div>
                                </div>
                            </td>
                        </tr>
                    </table>
            </div>

            <div>

            </div>
                做完主线大约有以下奖励（注：不同基础职业主线任务数量可能有一些差异，这里以战士职业为例）

                <p>金币：<strong>{{result.total.gold | gold}}</strong></p>
                <p>物品合计奖励</p>
                <div style="display:flex;justify-content:center">
                    <div class="Title">
                        <div v-for="(vv,kk) in result.total.items" :key="kk" class="titleitem">
                            <item-icon-tooltip
                                    :itemId="vv.itemId" 
                                    :itemStub="vv.item"
                                    :count="vv.count"
                                    :noLink="noLink">
                            </item-icon-tooltip>
                        </div>
                    </div>
                </div>
            <div>
                <h3>班达奖励</h3>
                    <table border="1" class="labryinth-table">
                        <thead>
                            <th>章节</th>
                            <th>奖励</th>
                            <th>点券</th>
                        </thead>
                        <tr v-for="(v,k) in result.list2" :key="k">
                            <td>{{v.chapterName}}</td>
                            <td>
                                <div class="Title">
                                    <div v-for="(vv,kk) in v.reward" :key="kk" class="titleitem">
                                        <item-icon-tooltip
                                                :itemId="vv.itemId" 
                                                :itemStub="vv.item"
                                                :count="vv.count"
                                                :noLink="noLink">
                                        </item-icon-tooltip>
                                    </div>
                                </div>
                            </td>
                            <td>
                                <div class="Title">
                                    <div v-for="(vv,kk) in v.mailItems" :key="kk" class="titleitem">
                                        <item-icon-tooltip
                                                :itemId="vv.itemId" 
                                                :itemStub="vv.item"
                                                :count="vv.count"
                                                :noLink="noLink">
                                        </item-icon-tooltip>
                                    </div>
                                </div>
                            </td>
                        </tr>
                    </table>
            </div>
        </template>
        <back-to-top class="topButton"><i class="fa fa-arrow-up"></i></back-to-top>
    </div>
</template>
<script>
import Vue from 'vue';
import GameInfo from '../api/GameInfo';
import GoBack from "@/components/util/GoBack.vue";
import ItemIconTooltip from '@/old/items/ItemIconTooltip.vue';
import Loader from "@/components/util/Loader";
import ItemCard from "@/old/items/ItemCard";
import BackToTop from '@/components/util/BacktoTop';
Vue.component('item-card', ItemCard);
Vue.component("load-indicator", Loader);
export default {
    created(){
        this.fetchData();
    },
    components: {
        GoBack,
        ItemIconTooltip,
        BackToTop
    },
    data(){
        return{
            result:null,
            noLink:false,
            loading:true,
        }
    },
    methods:{
        fetchData(){
            if(GameInfo.MainQuestCache){
                this.result = GameInfo.MainQuestCache;
                this.loading = false;
            }else{
            GameInfo.MainQuest(this.$store.state.regionCode,
                    (res) => {
                        this.result = res;
                        this.loading = false;
                    },
                    (err) => {
                        this.error = err;
                        this.loading = false;
                    });
            }
        }
    }
}
</script>
<style lang="less" scoped>
@import "../../less/core.less";
.Title {
    display: flex;
    flex-direction: row;

    .titleitem {
        flex: 0 0 auto;
    }
}

.labryinth-table{
    margin:0 auto;
    margin-bottom: 25px;
}
</style>